import { useDispatch, useSelector } from 'react-redux';

import { cryptoHistoryItemDeleted, cryptoByIdFetched, addFormDisplayed} from '../../slices/portfolioSlice';
import { setLocalData } from '../portfolioItemAddForm/PortfolioItemAddForm';
import plusImg from '../../media/plus.png'
import binImg from '../../media/bin.png'

import classes from '../../styles/app.module.css';


const PortfolioHistoryActions = (props) => {
    const dispatch = useDispatch();
    // const { cryptoList, assetsHistory } = useSelector(state => state.portfolio);

    return (
        <div 
            className={classes.portfolio__actions_menu} 
            style={{height: 79, right: 10, top: 0}}>
            <span style={{padding: '0'}}>
                <img 
                    style={{marginTop: 27, width: 24, height: 24}}
                    src={plusImg} 
                    alt="add"
                    onClick={() => {
                        dispatch(cryptoByIdFetched(props.coin.id))
                        dispatch(addFormDisplayed())
                    }}/>
            </span>
            <span>
                <img 
                    style={{marginTop: 27, width: 24, height: 24, }}
                    src={binImg} 
                    alt="remove"
                    onClick={() => {
                        console.log({
                            id: props.coin.transactionId, 
                            amount: props.coin.amount,
                            total: props.coin.total
                        });
                        dispatch(cryptoHistoryItemDeleted({
                            id: props.coin.id,
                            transactionId: props.coin.transactionId, 
                            amount: props.coin.amount,
                            total: props.coin.total
                        }))
                        // setLocalData()
                    }}
                    />
                </span>
        </div>
    )
}

export default PortfolioHistoryActions;