import { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Line } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,} from 'chart.js';

const CryptoChart = () => {
    const [holdings, setHoldings] = useState([]);
    const {cryptoList} = useSelector(state => state.portfolio);

    useEffect(() => {
        setHoldings([0, cryptoList.reduce((a,b) => a + b.holdings, 0)])
    // eslint-disable-next-line
    }, [])

    useEffect(() => {
        setHoldings(holdings => [holdings[1] || holdings, cryptoList.reduce((a,b) => a + b.holdings, 0)])
    // eslint-disable-next-line 
    }, [cryptoList])



    ChartJS.register(
        CategoryScale,
        LinearScale,
        PointElement,
        LineElement,
        Title,
        Tooltip,
        Legend
    );
    
    const options = {
        responsive: true,
        plugins: {
            legend: {
                display: false
            },
        },
        scales: {
            x: {
                grid: {
                    display: false
                }
            },
            y: {
                grid: {
                    display: false
                }
            }
        }
    };
    
    const labels = ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12' ];
    
    const data = {
        labels,
        datasets: [
            {
                label: 'Assets',
                data: holdings,
                borderColor: 'rgba(0, 128, 128, 0.8)',
                backgroundColor: 'white',
                maxWidth: '80%'
            }
        ],
        
    }
    return (
        <div 
            style={{marginBottom: 30}}>
            <Line 
                options={options}
                data={data}/>
        </div>
    );
};

export default CryptoChart;