import { useState, useEffect } from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {v4 as uuidv4} from 'uuid';
import CryptoJs from 'crypto-js';

import Header from "../header/Header";
import cryptoService from '../../services/cryptoService';
import PortfolioList from '../portfolioList/PortfolioList'
import PortfolioItemAddForm from '../portfolioItemAddForm/PortfolioItemAddForm';
import PortfolioSearchForm from '../portfolioSearchForm/PortfolioSearchForm';
import UserModal from '../userModal/UserModal';
import CryptoChart from '../cryptoChart/CryptoChart';
import { 
    addFormDisplayed, 
    searchFormDisplayed, 
    searchFormHided, 
    cryptoSelected, 
    portfolioPricesFetched, 
    savedAssetsChecked,
    savedAssetsHistoryChecked } from '../../slices/portfolioSlice';
import { messageDisplayed } from '../../slices/authSlice'

import classes from '../../styles/app.module.css';

const PortfolioPage = () => {
    const {getCurrency, getCrypto} = cryptoService();
    const dispatch = useDispatch();
    const {displaySearchForm, displayAddForm, cryptoList, assetsHistory} = useSelector(state => state.portfolio);
    const {isLogged, message} = useSelector(state => state.pseudoAuth);
    const [portfolioSearchList, setPortfolioSearchList] = useState([]);


    const onSetList = (data) => {
        setPortfolioSearchList(data);
    }

    const fetchData = () => {
        if (cryptoList.length !== 0) {
            dispatch(portfolioPricesFetched(cryptoList.map(i => i.id)))
        }
    }

    const checkSavedAssets = () => {
        if (cryptoList.length === 0 && localStorage.cryptoAssets && localStorage.cryptoAssets !== '[]') {
            const assetsData = localStorage.cryptoAssets;
            const bytes = CryptoJs.AES.decrypt(assetsData, 'tether')
            const decrData = JSON.parse(bytes.toString(CryptoJs.enc.Utf8))
            dispatch(savedAssetsChecked(decrData))
        }
        if (assetsHistory.length === 0 && localStorage.assetsHistory && localStorage.assetsHistory !== '[]') {
            const assetsHistory = localStorage.assetsHistory;
            const bytes = CryptoJs.AES.decrypt(assetsHistory, 'usdc')
            const decrData = JSON.parse(bytes.toString(CryptoJs.enc.Utf8))
            dispatch(savedAssetsHistoryChecked(decrData))
        }
    }

    useEffect(() => {
        checkSavedAssets()
        // dispatch(savedAssetsChecked());
        getCurrency(10)
            .then(data => setPortfolioSearchList(data))
            .catch(err => console.log(err))
    // eslint-disable-next-line
    }, [])

    useEffect(() => {
        fetchData();
    // eslint-disable-next-line 
    }, [cryptoList])
    
    useEffect(() => {
        const intervalId = setInterval(fetchData, 10000);

        return () => clearInterval(intervalId);
        // eslint-disable-next-line
    }, [])
    
    const searchCoins = portfolioSearchList?.map((coin, i) => {
        return (
            <li 
                key={uuidv4()}
                onClick={() => {
                    getCrypto(coin.id)
                    .then(coin => {
                        dispatch(cryptoSelected(coin))
                        dispatch(searchFormHided())
                        dispatch(addFormDisplayed())
                    })
                    
                }}>{coin.name}</li>
        )
    })

    return (
        <>
            <Header />
            <UserModal />
            <section className={classes.portfolio} style={{textAlign: 'center'}}>
                <div className={classes.body_hover} style={{display: `${displayAddForm || displaySearchForm ? 'block' : 'none'}`}}></div>
                <h1>Portfolio</h1>
                <div className={classes.portfolio__container}>
                    <CryptoChart/>
                    
                    <PortfolioSearchForm 
                        onSetList={onSetList} 
                        searchCoins={searchCoins}
                        style={{display: `${displaySearchForm ? 'flex' : 'none'}`}}
                        />
                    
                    <PortfolioItemAddForm style={{display: `${displayAddForm ? 'flex' : 'none'}`}}/>

                    <PortfolioList/>
                    <div className={classes.portfolio__currency_add}>
                        <span>Add your cryptocurrency</span>
                        <button 
                            className={classes.btn}
                            onClick={() => {
                                if (isLogged) {
                                    dispatch(searchFormDisplayed())
                                } else {
                                    dispatch(messageDisplayed(true))
                                }
                                }}>+</button>
                        <p style={{color: 'red', display: `${message ? 'block' : 'none'}`}}>Please, log in!</p>
                    </div>
                </div>
            </section>
        </>
    );
};

export default PortfolioPage;