import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import {v4 as uuidv4} from 'uuid';
import CryptoJs from 'crypto-js';

import { savedAssetsChecked, savedAssetsHistoryChecked, portfolioPricesFetched} from '../../slices/portfolioSlice';

import Header from '../header/Header';
import PortfolioAssetsListItem from '../portfolioAssetsListItem/PortfolioAssetsListItem';
import PortfolioItemAddForm from '../portfolioItemAddForm/PortfolioItemAddForm';

import classes from '../../styles/app.module.css';


const PortfolioAssetHistoryPage = () => {
    const dispatch = useDispatch();
    
    const { cryptoId } = useParams();
    const { assetsHistory, cryptoList, displayAddForm, portfolioPrices } = useSelector(state => state.portfolio);

    const loc = document.location.href.replace(/.+?(?=portfolio)(portfolio.)/g, '')
    const crypto = assetsHistory.filter(item => item.id === (cryptoId || loc));
    const asset = cryptoList.find(coin => coin.id === (cryptoId || loc));
    const assetPrice = portfolioPrices.find(i => i.id === asset?.id)?.usd || 0;
    const result = asset?.amount * assetPrice - asset?.total;

    const checkSavedAssets = () => {
        if (cryptoList.length === 0 && localStorage.cryptoAssets && localStorage.cryptoAssets !== '[]') {
            const assetsData = localStorage.cryptoAssets;
            const bytes = CryptoJs.AES.decrypt(assetsData, 'tether')
            const decrData = JSON.parse(bytes.toString(CryptoJs.enc.Utf8))
            dispatch(savedAssetsChecked(decrData))
        }
        if (assetsHistory.length === 0 && localStorage.assetsHistory && localStorage.assetsHistory !== '[]') {
            const assetsHistory = localStorage.assetsHistory;
            const bytes = CryptoJs.AES.decrypt(assetsHistory, 'usdc')
            const decrData = JSON.parse(bytes.toString(CryptoJs.enc.Utf8))
            dispatch(savedAssetsHistoryChecked(decrData))
        }
    }

    useEffect(() => {
        const fetchPrices = () => {
            dispatch(portfolioPricesFetched(cryptoList.map(i => i.id)));
        }
        checkSavedAssets()
        const intervalId = setInterval(fetchPrices, 10000);

        return () => clearInterval(intervalId);
        // eslint-disable-next-line
    }, [])

    const assets = crypto.map((asset, i) => {
        return (
            <PortfolioAssetsListItem 
                asset={asset} 
                key={uuidv4()}
                />
            )
    })
    
    return (
        <>
            <Header/>
            <section className={classes.portfolioAssets__container}>
                <div className={classes.body_hover} style={{display: `${displayAddForm ? 'block' : 'none'}`}}></div>
                <PortfolioItemAddForm style={{display: `${displayAddForm ? 'flex' : 'none'}`}}/>
                <div className={classes.portfolioAssets__total}>
                    <h3>{crypto[0]?.id.toUpperCase()}</h3>
                    <div>
                        <h2 >{asset?.holdings ? asset?.holdings.toFixed(2) : cryptoList.filter(i => i.id === loc).reduce((a, b) => a + (b.amount * b.price), 0).toFixed(2)} $</h2>
                        <span style={{color: 'teal'}}>Total holdings</span>
                    </div>
                    <div className={classes.portfolioAssets__total_box}>
                        <span>{asset?.amount} {asset?.symbol.toUpperCase()}</span>
                        <span>{asset?.price} $</span>
                        <span style={{color: `${result > 0 ? 'green' : 'red'}`}}>{result.toFixed(4)} $</span>
                    </div>
                    <div className={classes.portfolioAssets__total_box} style={{position: 'relative'}}>
                        <span>Amount</span>
                        <span>Avg.buy price</span>
                        <span>Total profit / loss</span>
                        
                    </div>
                </div>
                <PortfolioTable/>
                <ul>
                    {assets}
                </ul>
            </section>
        </>
        
        
    );
};

const PortfolioTable = () => {
    return (
        <div className={classes.portfolio__table_header}
            style={{textAlign: 'center'}}>
            <span>Type</span>
            <span style={{display: 'block'}}>Price</span>
            <span>Amount</span>
            <span>Actions</span>
        </div>
    ) 
}

export default PortfolioAssetHistoryPage;