import { useState} from 'react';

import PortfolioHistoryActions from '../portfolioHistoryActions/PortfiolioHistoryActions';

import classes from '../../styles/app.module.css';

const PortfolioAssetsListItem = (props) => {
    const [toggleAction, setToggleAction] = useState(false);
    const {asset} = props;
    return (
        <li>
            <span style={{color: `${asset.type === 'buy' ? 'teal' : 'red'}`}}>{asset.type.charAt(0).toUpperCase() + asset.type.slice(1)}</span>
            <span>{asset.price} $</span>
            <span>{asset.amount} {asset.symbol.toUpperCase()}</span>
            <span style={{
                display: 'flex', 
                overflow: 'hidden', 
                position: 'relative', 
                flex: '1',
            }}>
                <span 
                    style={{padding: 0}}
                    className={classes.portfolio__actions}
                    onClick={() => setToggleAction(toggleAction => !toggleAction)}>...</span>
                {toggleAction ? <PortfolioHistoryActions coin={asset} /> : null}
            </span>
        </li>
    )
}

export default PortfolioAssetsListItem;