import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    displayModal: 'none',
    loginModal: false,
    isLogged: false,
    loginName: '',
    message: false,
    authDataEmpty: "We don't know ! :)"
}

const authSlice = createSlice({
    name: 'pseudoAuth',
    initialState,
    reducers: {
        modalDisplayed: state => {state.displayModal = 'block'},
        modalHided: state => {state.displayModal = 'none'},
        loginModalSetted: (state, action) => {state.loginModal = action.payload},
        loginSetted: (state, action) => {state.loginName = action.payload},
        userLogged: (state, action) => {state.isLogged = action.payload},
        messageDisplayed: (state, action) => {state.message = action.payload},
        authDataStatusChanged: (state, action) => {state.authDataEmpty = action.payload},
    }
})

const {actions, reducer} = authSlice;

export default reducer;

export const {
    modalDisplayed,
    modalHided,
    loginModalSetted,
    loginSetted,
    userLogged,
    messageDisplayed,
    authDataStatusChanged,
} = actions