import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { useHttp } from '../hooks/http.hook';
import CryptoJs from 'crypto-js';

const initialState = {
    portfolioLoading: 'idle',
    amountError: false,
    displaySearchForm: false,
    displayAddForm: false,
    selectedCrypto: {},
    cryptoList: [],
    portfolioPrices: [],
    isSorted: false,
    assetsHistory: []
}

export const portfolioPricesFetched = createAsyncThunk(
    'portfolio/portfolioPricesFetched',
    async (query) => {
        if (query) {
            const { request } = useHttp();
            return await request(`https://api.coingecko.com/api/v3/simple/price?ids=${query}&vs_currencies=usd`, 
                'GET', null, {
                    'accept': 'application/json',
                }
            );
        }
    }
)

export const cryptoByIdFetched = createAsyncThunk(
    'portfolio/cryptoByIdFetched',
    async (id) => {
        const { request } = useHttp();
        const result = await request(`https://api.coingecko.com/api/v3/coins/${id}?community_data=false&developer_data=false&sparkline=false`, 
        'GET', null, {
            'accept': 'application/json',
        }
    );
    const _transformCrypto = (crypto) => {
        return {
            id: crypto.id,
            symbol: crypto.symbol,
            descr: crypto.description.en,
            price: crypto.market_data.current_price.usd,
            current_price: crypto.market_data.current_price.usd,
            ath_price: crypto.market_data.ath.usd,
            ath_date: crypto.market_data.ath_date.usd,
            price_change_percentage_24h: crypto.market_data.price_change_percentage_24h,
            name: crypto.name,
            image: crypto.image,
            links: crypto.links.homepage,
            market_cap: crypto.market_data.market_cap.usd,
            market_cap_rank: crypto.market_cap_rank,
            markets: crypto.tickers.map(market => [
                market.market.name, 
                market.target,
                market.last,
                market.volume,
                market.trade_url
            ])
        }
    }
    const res = _transformCrypto(result);
    return res;
    }
)

const portfolioSlice = createSlice({
    name: 'portfolio',
    initialState,
    reducers: {
        searchFormDisplayed: state => {state.displaySearchForm = true},
        searchFormHided: state => {state.displaySearchForm = false},
        addFormDisplayed: state => {
            state.amountError = false
            state.displayAddForm = true
        },
        addFormHided: state => {state.displayAddForm = false},
        cryptoSelected: (state, action) => {state.selectedCrypto = action.payload},
        cryptoAdded: (state, action) => {state.cryptoList.push(action.payload)},
        cryptoModified: (state, action) => {
            state.amountError = false;
            const item = state.cryptoList.find(item => item.name === action.payload.name);
            if (item) {
                if (action.payload.type === 'buy') {
                    const res = (item.price + action.payload.price) / 2;
                    item.price = res;
                    item.amount = item.amount + action.payload.amount;
                    item.total = item.total + action.payload.total;
                    state.assetsHistory.push(action.payload)
                    state.displayAddForm = false
                }
                if (action.payload.type === 'sell') {
                    if (+item.amount - +action.payload.amount >= 0) {
                        item.amount = +item.amount - +action.payload.amount;
                        item.total = +item.total - +action.payload.total;
                        state.assetsHistory.push(action.payload)
                        state.displayAddForm = false
                    } else {
                        state.amountError = true;
                    }
                }
            } else {
                state.cryptoList.push(action.payload)
                state.assetsHistory.push(action.payload)
                state.displayAddForm = false
            }
            const encData = CryptoJs.AES.encrypt(JSON.stringify(state.cryptoList), 'tether').toString()
            const encHistory = CryptoJs.AES.encrypt(JSON.stringify(state.assetsHistory), 'usdc').toString()
            localStorage.setItem('cryptoAssets', encData)
            localStorage.setItem('assetsHistory', encHistory)
        },
        savedAssetsChecked: (state, action) => {
            // if (state.cryptoList.length === 0 && localStorage.cryptoAssets && localStorage.cryptoAssets !== '[]') {
            //     const assetsData = localStorage.cryptoAssets;
            //     const bytes = CryptoJs.AES.decrypt(assetsData, 'tether')
            //     const decrData = JSON.parse(bytes.toString(CryptoJs.enc.Utf8))
            //     state.cryptoList = decrData;
            // }
            // if (state.assetsHistory.length === 0 && localStorage.assetsHistory && localStorage.assetsHistory !== '[]') {
            //     const assetsHistory = localStorage.assetsHistory;
            //     const bytes = CryptoJs.AES.decrypt(assetsHistory, 'usdc')
            //     const decrData = JSON.parse(bytes.toString(CryptoJs.enc.Utf8))
            //     state.assetsHistory = decrData;
            // }
            state.cryptoList = action.payload;
        },
        savedAssetsHistoryChecked: (state, action) => {state.assetsHistory = action.payload;},
        assetsSorted: (state, action) => {
            if (!state.isSorted) {
                state.cryptoList.sort((a,b) => {
                    if (a[action.payload] > b[action.payload]) {
                        return -1;
                    } 
                    if (a[action.payload] < b[action.payload]) {
                        return 1;
                    }
                    return 0;
                })
                state.isSorted = !state.isSorted;
            } else {
                state.cryptoList.sort((a,b) => {
                    if (a[action.payload] > b[action.payload]) {
                        return 1;
                    } 
                    if (a[action.payload] < b[action.payload]) {
                        return -1;
                    }
                    return 0;
                })
                state.isSorted = !state.isSorted;
            }
        },
        cryptoAssetDeleted: (state, action) => {
            state.cryptoList = state.cryptoList.filter(item => item.id !== action.payload) || [];
            state.assetsHistory = state.assetsHistory.filter(item => item.id !== action.payload);
            const encData = CryptoJs.AES.encrypt(JSON.stringify(state.cryptoList), 'tether').toString()
            const encHistory = CryptoJs.AES.encrypt(JSON.stringify(state.assetsHistory), 'usdc').toString()
            localStorage.setItem('cryptoAssets', encData)
            localStorage.setItem('assetsHistory', encHistory)
        },
        cryptoHistoryDeleted: (state, action) => {
            state.assetsHistory = state.assetsHistory.filter(item => item.id !== action.payload);
            const encHistory = CryptoJs.AES.encrypt(JSON.stringify(state.assetsHistory), 'usdc').toString()
            localStorage.setItem('assetsHistory', encHistory)
        },
        cryptoHistoryItemDeleted: (state, action) => {
            const item = state.cryptoList.find(item => item.id === action.payload.id);
            state.assetsHistory = state.assetsHistory.filter(item => item.transactionId !== action.payload.transactionId);
            item.amount = item.amount - action.payload.amount;
            item.total = item.total - action.payload.total;

            if (item.amount === 0) {
                state.cryptoList = state.cryptoList.filter(item => item.id !== action.payload.id)
            }
            
            const encData = CryptoJs.AES.encrypt(JSON.stringify(state.cryptoList), 'tether').toString()
            const encHistory = CryptoJs.AES.encrypt(JSON.stringify(state.assetsHistory), 'usdc').toString()
            localStorage.setItem('cryptoAssets', encData)
            localStorage.setItem('assetsHistory', encHistory)
        },
    },
    extraReducers: (builder) => {
        builder
            .addCase(portfolioPricesFetched.pending, state => {state.portfolioLoading = 'loading'})
            .addCase(portfolioPricesFetched.fulfilled, (state, action) => {
                const priceList = Object.entries(action.payload).map(i => {
                    const obj = {};
                    obj['id'] = i[0];
                    obj['usd'] = i[1].usd;
                    return obj;
                })
                state.portfolioLoading = 'idle';
                state.portfolioPrices = [];
                state.portfolioPrices.push(priceList)
                state.portfolioPrices = state.portfolioPrices.flat();
                state.portfolioPrices.forEach(item => {
                    const asset = state.cryptoList?.find(i => i.id === item.id)
                    asset.holdings = item.usd * asset?.amount
                })
            })
            .addCase(portfolioPricesFetched.rejected, state => {state.portfolioLoading = 'error'})
            .addCase(cryptoByIdFetched.pending, state => {state.portfolioLoading = 'loading'})
            .addCase(cryptoByIdFetched.fulfilled, (state, action) => {state.selectedCrypto = action.payload})
            .addCase(cryptoByIdFetched.rejected, state => {state.portfolioLoading = 'error'})
            .addDefaultCase(() => {})
    }
})

const {actions, reducer} = portfolioSlice;

export default reducer;

export const {
    searchFormDisplayed,
    searchFormHided,
    addFormDisplayed,
    addFormHided,
    cryptoSelected,
    cryptoAdded,
    cryptoModified,
    savedAssetsChecked,
    savedAssetsHistoryChecked,
    assetsSorted,
    cryptoAssetDeleted,
    cryptoHistoryDeleted,
    cryptoHistoryItemDeleted
} = actions


        